.siteright {
    background-color: #FFCA00;
    width: 55%;    
    padding: 0 50px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

@media(max-width: 575px){
    .siteright {
        width: 100%;
        display: block;
        background: transparent;
    }
}

.contentheadding {
    margin-left: 100px;
    height: 350px;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
}

.contentheadding h2 {
    margin-top: 100px;
    margin-bottom: 100px;
}

.contentheadding h2 span {
    color: #fff;
}

@media(max-width: 767px){
    .contentheadding {
        margin-left: 35px;
    }
}

.newslatter {
    margin-left: 100px;
}

.newslatter h4 {
    color: #fff;
    margin-bottom: 20px;
}



@media(max-width: 767px){
    .newslatter {
        margin-left: 35px;
    }
}


@media(max-width: 575px){
    .newslatter {
        margin-bottom: 40px;
        margin-left: 0;
        text-align: center;
    }
    .newslatter h4 {
        color: #000;
    }
}

.form {
    display: flex;
    flex-wrap: nowrap;
    margin-bottom: 30px;
    width: 400px;
}

@media(max-width: 575px){
    .form {
        margin-bottom: 0px;
    }
}

@media(max-width: 1200px){
    .form {
        width: 350px;
    }
}

@media(max-width: 767px){
    .form {
        width: 300px;
    }
}

@media(max-width: 575px){
    .form {
        width: 100%;
        margin: auto;
    }
}


.form input {
    height: 55px;
    flex: 1 1 70%;
    border-radius: 6px 0 0 6px;
    border: 0px;
    background: #fff;
    color: #000;
    transition: 0.3s;
    padding: 0 15px;
    margin-right: 0px;
    font-size: 16px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.32);
}

.form input:focus {
    border: 0px;
}

.form button {
    height: 55px;
    flex: 1 1 30%;
    background: #000;
    color: #fff;
    border: 0px;
    border-radius: 0 6px 6px 0;
    font-weight: 700;
    font-size: 14px;
    text-transform: uppercase;
    cursor: pointer;
    transition: 0.3s;
}

.form button:hover {
    background: #FFF200;
    color: #000;
}