.siteleft {
    width: 45%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 40px;
}

@media(max-width: 575px){
    .siteleft {
        width: 100%;
        display: block;
        text-align: center;
        position: relative;
    }
    .siteleft::before {
        position: absolute;
        content: "";
        left: 0;
        top: 0;
        width: 100%;
        height: 850px;
        z-index: -1;
        background: #FFCA00;
    }
}


.siteleft a {
    margin-bottom: 110px;
}
@media(max-width: 767px){
    .siteleft a {
        margin-bottom: 80px;
    }
}

.siteleft a img {
    height: 85px;
    width: auto;
}


@media(max-width: 991px){
    .siteleft a img {
        height: 65px;
    }
}

.mobileversion {
    display: none;
}
@media(max-width: 575px){
    .mobileversion {
        display: block;
        margin-bottom: 60px;
    }
}


.mobilescreen {
    position: relative;
}
.frame {
    width: 300px;
    height: auto;
    margin-right: -140px;
    z-index: 1;
    position: relative;
}
@media(max-width: 991px){
    .frame {
        width: 270px;
    }
}
@media(max-width: 767px){
    .frame {
        margin-right: -100px;
    }
}
@media(max-width: 575px){
    .frame {
        margin-right: 0px;
    }
}

.screens {
    position: absolute;
    left: 20px;
    top: 20px;
    width: 260px;
    margin-left: 0;
}

@media(max-width: 991px){
    .screens {
        width: 232px;
        height: auto;
    }
}

@media(max-width: 575px){
    .screens {
        left: 50%;
        transform: translateX(-50%);
    }
}




/* Optional */

.contentheadding {
    height: 350px;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
}

.contentheadding h2 {
    margin-top: 100px;
    margin-bottom: 200px;
    font-size: 36px;
}

.contentheadding h2 span {
    color: #fff;
}